<template>
    <main class="mainContain">
      <div class="I_leftBar">
        <div v-show="barhide" class="I_FunChoose">
          <h6>功能菜单的选择</h6>
          <div>
            <div class="FunTitle" @click="Fundisplay(1)">-- 常用小功能</div>
            <div class="FunCont" v-show="firstFloor" @click="changPage(1)">--计算器</div>
            <div class="FunCont" v-show="firstFloor" @click="changPage(2)">--日历</div>
            <div class="FunCont" v-show="firstFloor" @click="changPage(3)">--备忘录</div>
          </div>
          <div>
            <div class="FunTitle" @click="Fundisplay(2)">-- 绘图功能</div>
            <div class="FunCont" v-show="secondFloor" @click="changPage(10)">--知识图</div>
            <div class="FunCont" v-show="secondFloor" @click="changPage(11)">--条形图</div>
            <div class="FunCont" v-show="secondFloor" @click="changPage(12)">--扇形图</div>
          </div>
        </div>
        <div>
          <img src="../../assets/img/Left_arrow.svg" alt="隐藏按钮" v-show="barhide" @click="displays(1)">
          <img src="../../assets/img/right_arrow.svg" alt="展开按钮" v-show="bardisplay" @click="displays(2)">
        </div>
      </div>
      <div class="I_centerBar">
        <router-view></router-view>
      </div>
    </main>
</template>>

<script>
// import api from '@/assets/Js/home/home'
// import axios from 'axios'
import * as d3 from 'd3'
export default {
  name: 'KnowledgeA',
  data () {
    return {
      barhide: true,
      bardisplay: false,
      firstFloor: false,
      firstFloorNum1: 0,
      firstFloorNum2: 0,
      secondFloor: false
    }
  },
  methods: {
    displays (num) {
      const _this = this
      switch (num) {
        case 1:
          d3.select('.I_leftBar').transition().duration(200).style('width', '2rem')
          d3.select('.I_centerBar').transition().duration(200).style('width', '98%')
          _this.barhide = false
          _this.bardisplay = true
          break
        case 2:
          d3.select('.I_leftBar').transition().duration(200).style('width', '10%')
          d3.select('.I_centerBar').transition().duration(200).style('width', '90%')
          setTimeout(function () {
            _this.barhide = true
            _this.bardisplay = false
          }, 200)
          break
      }
    },
    Fundisplay (num) {
      const _this = this
      switch (num) {
        case 1:
          if (_this.firstFloorNum1 === 0) {
            _this.firstFloor = true
            _this.firstFloorNum1 = 1
            break
          } else {
            _this.firstFloor = false
            _this.firstFloorNum1 = 0
            break
          }
        case 2:
          if (_this.firstFloorNum2 === 0) {
            _this.secondFloor = true
            _this.firstFloorNum2 = 1
            break
          } else {
            _this.secondFloor = false
            _this.firstFloorNum2 = 0
            break
          }
      }
    },
    changPage (num) {
      const _this = this
      switch (num) {
        case 1:
          _this.$router.push('/home/KnowledgeA/calculator')
          break
        case 2:
          _this.$router.push('/home/KnowledgeA/calendar')
          break
        case 3:
          _this.$router.push('/home/KnowledgeA/memo')
          break
        case 10:
          _this.$router.push('/home/KnowledgeA/drawKG')
          break
      }
    }
  },
  mounted () {

  }
}
</script>

<style scoped >
/* 主体内容的设置 */
@import url("../../assets/Css/home/mineK.css");
</style>
